<template>
	<div id="vip-redpacket-index">
		<public-header :title="vipRedpacket.params.title" v-if="!isWeixin"></public-header>
		<!--未购买会员-->
		<div class="container" :style="{'padding-bottom':(showFixedBtn ? 65 : 0) + 'px'}" v-if="!member.vipRedpacket_status">
			<div class="userinfo">
				<div class="bg"></div>
				<div class="info">
					<div class="member margin-15-b flex-lr">
						<div class="avatar">
							<span class="icon icon-crownfill"></span>
						</div>
						<div class="other flex-1">
							<div class="mobile">{{member.mobile_secret ? member.mobile_secret : member.nickname}}</div>
							<div class="status">暂未开通会员</div>
						</div>
					</div>
					<div class="card flex-lr">
						<div>
							<div class="font-bold font-18">{{vipRedpacket.params.title}}</div>
							<div class="font-14 margin-10-t">{{vipRedpacket.params.placeholder}}</div>
						</div>
						<div>
							<div class="font-14">
								最低{{Lang.dollarSign}}<span class="font-bold font-20">{{vipRedpacket.params.price}}</span>/月
							</div>
							<router-link :to="util.getUrl({path: '/plugin/pages/vipRedpacket/create'})" tag="div" class="buy">立即开通</router-link>
						</div>
					</div>
				</div>
			</div>
			<div class="power-wrap padding-15-lr">
				<div class="title">尊享会员享<span class="power-num">3</span>大特权</div>
				<ul class="powers">
					<li class="power-item" v-for="(item, index) in vipRedpacket.powers" :key="index">
						<div class="icon-wrap">
							<i class="icon" :class="item.icon"></i>
						</div>
						<div>{{item.text}}</div>
					</li>
				</ul>
			</div>
			<div class="power-content">
				<div class="number">特权1</div>
				<div class="title">每月享{{vipRedpacket.redpacket.discount * vipRedpacket.redpacket.num}}{{Lang.dollarSignCn}}无门槛红包</div>
				<div class="tips flex-lr">
					<ul>
						<li>
							<i class="icon icon-roundcheckfill"></i>
							<span>无门槛</span>
						</li>
						<li class="margin-5-t">
							<i class="icon icon-roundcheckfill"></i>
							<span>可与满减/折扣同享</span>
						</li>
					</ul>
					<div>
						<div class="power-take">
							<div class="bg-1"></div>
							<div class="bg-2"></div>
							<div class="take-num">
								<span class="c-danger font-20">{{vipRedpacket.redpacket.discount}}</span> {{Lang.dollarSignCn}} x <span class="c-danger font-20">{{vipRedpacket.redpacket.num}}</span> 张
							</div>
						</div>
					</div>
				</div>
			</div>
			<div class="power-content" v-if="vipRedpacket.params.exchangeStatus == 1">
				<div class="number">特权2</div>
				<div class="title">会员红包可升级至更大金额</div>
				<div class="tips flex-lr">
					<ul>
						<li>
							<i class="icon icon-roundcheckfill"></i>
							<span>会员可免费升级</span>
						</li>
						<li class="margin-5-t">
							<i class="icon icon-roundcheckfill"></i>
							<span>海量商家支持升级</span>
						</li>
					</ul>
					<div>
						<div class="exchange">
							<i class="icon icon-forwardfill"></i>
							<div class="from">
								<span class="c-danger font-14">5</span>{{Lang.dollarSignCn}}
							</div>
							<div class="to">
								<span class="c-danger font-20">6~10</span>{{Lang.dollarSignCn}}
							</div>
						</div>
					</div>
				</div>
				<div class="store-wrap" v-if="stores.length > 0">
					<van-row>
						<van-col span="3" class="store-item" v-for="(item, index) in stores" :key="index">
							<img class="img-100" :src="item.logo" alt="">
						</van-col>
					</van-row>
					<div class="font-12 margin-5-t">*实际商家和升级金额以外卖首页为准</div>
					<div class="bg"></div>
				</div>
			</div>
			<div class="power-content" v-if="meals.length > 0">
				<div class="number">特权3</div>
				<div class="title">底价购买会员加量包</div>
				<div class="flex margin-5-t">
					<i class="icon icon-warnfill"></i>
					<span class="font-12">开通会员后即可购买和使用</span>
				</div>
				<div class="meals">
					<div class="meal-item" v-for="(item, index) in meals" :key="index">
						<div class="redpacket">
							<div class="flex-center padding-10-t">
								<span class="font-20">{{item.discount}}</span>
								<span class="font-12">{{Lang.dollarSignCn}}</span>
								<span class="padding-5-lr">x</span>
								<span class="font-20"> {{item.num}}</span>
								<span class="font-12">张</span>
							</div>
							<div class="redpacket-title">{{item.name}}</div>
							<div class="line"></div>
						</div>
						<div class="margin-10-t font-bold">{{item.title}}</div>
						<div class="price-wrap">
							<div class="price margin-5-r">
								{{Lang.dollarSign}}<span class="font-20 font-500">{{item.price}}</span>
							</div>
							<div class="old-orice font-12 c-gray line-through">
								{{Lang.dollarSign}}<span>{{item.old_price}}</span>
							</div>
						</div>
						<div class="label">限时活动</div>
					</div>
				</div>
			</div>
			<div class="flex-center padding-15-b">
				<span class="font-14" @click="onTogglePopup('agreement')">常见问题与说明</span>
				<i class="icon icon-right c-gray font-12"></i>
			</div>
			<div class="button-fixed" :class="{show: showFixedBtn}">
				<div class="inner flex-lr">
					<div class="tips">
						<span class="font-bold font-16">尊享会员</span>
						<span>最低{{Lang.dollarSign}}</span>
						<span class="font-20">15</span>
						<span>/月</span>
					</div>
					<router-link :to="util.getUrl({path: '/plugin/pages/vipRedpacket/create'})" tag="div" class="buy">立即开通</router-link>
				</div>
			</div>
		</div>
		<!--已购买会员-->
		<div class="container" v-else>
			<div class="userinfo">
				<div class="bg"></div>
				<div class="info">
					<div class="member margin-15-b flex-lr">
						<div class="avatar">
							<img :src="member.avatar" class="img-100">
						</div>
						<div class="other flex-1">
							<div class="mobile">{{member.mobile_secret ? member.mobile_secret : member.nickname}}</div>
							<div class="status">
								当前已开通会员
								<span class="font-12 padding-5-lr">|</span>
								<router-link tag="span" :to="util.getUrl({path: '/plugin/pages/vipRedpacket/record'})">购买记录</router-link>
							</div>
						</div>
					</div>
					<ul class="redpacket-total flex-lr">
						<li class="padding-15-r">
							<router-link :to="util.getUrl({path: '/plugin/pages/vipRedpacket/redpacket'})" tag="div">
								可用红包<span class="font-20 font-bold padding-5-lr">{{member.redpacket_num_avaliable}}</span>张
								<i class="icon icon-right font-12 c-gray margin-15-l"></i>
							</router-link>
							<div class="margin-10-t font-12">{{member.endtime_day}}到期</div>
						</li>
						<li class="padding-15-l">
							<router-link :to="util.getUrl({path: '/plugin/pages/vipRedpacket/redpacket'})" tag="div">
								已使用红包<span class="font-20 font-bold padding-5-lr">{{member.redpacket_num_noavaliable}}</span>张
								<i class="icon icon-right font-12 c-gray margin-15-l"></i>
							</router-link>
							<div class="margin-10-t font-12 c-gray">下单享优惠</div>
						</li>
					</ul>
					<div class="card flex-lr">
						<div>
							<div class="font-bold font-18">美团会员·尊享版</div>
							<div class="in-use">
								<i class="icon icon-roundcheckfill"></i>
								<span class="font-12">使用中</span>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div class="power-wrap padding-15-lr">
				<div class="title">尊享会员特权</div>
			</div>
			<div class="power-content">
				<div class="number">特权1</div>
				<div class="title">{{vipRedpacket.redpacket.num}}张{{vipRedpacket.redpacket.discount}}{{Lang.dollarSignCn}}会员红包</div>
				<div class="font-14 margin-10-t">本月的会员红包已发放</div>
			</div>
			<div class="power-content">
				<div class="number">特权2</div>
				<div class="title">低价购买会员加量包</div>
				<div class="flex margin-5-t">
					<i class="icon icon-warnfill"></i>
					<span class="font-12">
						请在<span class="c-danger">{{member.endtime_day}}前</span>购买使用
					</span>
				</div>
				<div class="meals">
					<div :class="{'meal-item': 1, active: item.buyed == 1}" v-for="(item, index) in meals" :key="index">
						<div class="redpacket">
							<div class="flex-center padding-10-t">
								<span class="font-20">{{item.discount}}</span>
								<span class="font-12">{{Lang.dollarSignCn}}</span>
								<span class="padding-5-lr">x</span>
								<span class="font-20"> {{item.num}}</span>
								<span class="font-12">张</span>
							</div>
							<div class="redpacket-title">{{item.name}}</div>
							<div class="line"></div>
						</div>
						<div class="margin-10-t font-bold">{{item.title}}</div>
						<div class="price-wrap">
							<div class="price margin-5-r">
								{{Lang.dollarSign}}<span class="font-20 font-500">{{item.price}}</span>
							</div>
							<div class="old-orice font-12 c-gray line-through">
								{{Lang.dollarSign}}<span>{{item.old_price}}</span>
							</div>
						</div>
						<div class="label">限时活动</div>
						<div class="buy"v-if="item.buyed == 1">已购买</div>
						<div class="buy" @click="onBuyMeal(item.id)" v-else>购买</div>
					</div>
				</div>
			</div>
			<div class="power-content" v-if="vipRedpacket.params.exchangeStatus == 1">
				<div class="number">特权3</div>
				<div class="title">会员红包免费升级</div>
				<div class="store-redpacket">
					<div class="redpacket-item flex-lr" v-for="(item, index) in exchanges.data" :key="index">
						<div class="left">
							<div class="logo">
								<img class="img-100" :src="item.logo" alt="">
							</div>
							<div class="info">
								<div>
									<div class="title-wrap w-100 flex">
										<div class="store-title ellipsis">{{item.title}}</div>
										<i class="icon icon-right font-12 c-gray margin-5-l"></i>
									</div>
									<div class="flex font-12 margin-5-t">
										<div class="score">
											<i class="icon icon-favorfill"></i>
											<span>{{item.score}}</span>
										</div>
										<span class="c-gray">月售{{item.sailed}}</span>
									</div>
								</div>
								<div class="w-100 font-12 ellipsis">
									<div class="label">优惠可叠加</div>
									<span class="">{{item.activity}}</span>
								</div>
							</div>
						</div>
						<div class="right">
							<div class="flex c-danger">
								<span class="font-16">{{Lang.dollarSign}}{{vipRedpacket.redpacket.discount}}</span>
								<span class="jiantou"></span>
								<span class="font-20 ">{{Lang.dollarSign}}{{item.discount}}</span>
							</div>
							<div class="btn-exchange" @click="onExchange(item.id)">升级</div>
							<div class="font-12 c-gray">需1张会员红包</div>
						</div>
					</div>
				</div>
				<div class="flex-center padding-10-t">
					<template v-if="!exchanges.finished">
						<span class="font-14 margin-5-r" @click="onLoadMore" v-if="!exchanges.finished">查看更多</span>
						<i class="icon icon-unfold font-12 c-gray"></i>
					</template>
					<template v-else>
						<span class="font-14 margin-5-r c-gray">没有更多了</span>
					</template>

				</div>
			</div>
			<div class="flex-center padding-15-b">
				<span class="font-14" @click="onTogglePopup('agreement')">常见问题与说明</span>
				<i class="icon icon-right c-gray font-12"></i>
			</div>
		</div>
		<!--购买加量包弹出层-->
		<van-popup v-model="popup.meal" position="bottom" class="popup-meal">
			<div class="popup-title van-hairline--bottom">
				购买加量包
				<i class="icon icon-close" @click="onTogglePopup('meal')"></i>
			</div>
			<div class="popup-content">
				<div class="meal flex-lr">
					<div class="left">
						<div class="font-16 font-500">{{mealActive.title}}</div>
						<div class="use-time">有效期至：{{member.endtime_day}}</div>
						<div class="font-12 c-gray">会员红包支持金额升级</div>
					</div>
					<div>
						<div class="redpacket">
							<div class="flex-center padding-10-t">
								<span class="font-20">{{mealActive.discount}}</span>
								<span class="font-12">{{Lang.dollarSignCn}}</span>
								<span class="padding-5-lr">x</span>
								<span class="font-20"> {{mealActive.num}}</span>
								<span class="font-12">张</span>
							</div>
							<div class="redpacket-title">{{mealActive.name}}</div>
							<div class="line"></div>
						</div>
					</div>
				</div>
				<div class="van-hairline--top padding-15-tb">
					<div class="flex-lr">
						<div class="">售卖价</div>
						<div class="flex">
							<span class="label">限时特惠</span>
							<span class="line-through font-12 c-gray margin-5-r">{{Lang.dollarSign}}{{mealActive.old_price}}</span>
							<span>{{Lang.dollarSign}}{{mealActive.price}}</span>
						</div>
					</div>
					<div class="flex-lr margin-15-t">
						<div>加量包抵扣券</div>
						<div class="c-gray">暂无可用</div>
					</div>
				</div>
				<div class="btn-wrap">
					<div class="flex-center use-day">
						<i class="icon icon-warn margin-5-r"></i>
						购买后请在<span class="c-danger">{{member.endtime_day}}</span>前使用
					</div>
					<div class="inner flex-lr">
						<div class="tips">已优惠{{Lang.dollarSign}}{{mealActive.discount_price}}</div>
						<div class="buy" @click="onSubmit">
							去支付{{Lang.dollarSign}}<span class="font-20">{{mealActive.price}}</span>
						</div>
					</div>
				</div>
			</div>
		</van-popup>
		<!--红包升级弹出层-->
		<van-popup v-model="popup.exchange" class="popup-exchange">
			<div class="title text-center font-16 font-bold">会员红包金额升级</div>
			<div class="line-12 margin-15-tb">当前还有{{member.redpacket_num_avaliable_notstore}}张尊享会员红包，升级需要消耗1张</div>
			<div class="redpacket">
				<div class="from">
					<div class="discount">{{Lang.dollarSign}} <span class="font-20">{{redpacketActive.discount}}</span></div>
					<div class="font-12 padding-10-tb text-center">会员红包</div>
					<div class="line"></div>
				</div>
				<i class="icon icon-forwardfill"></i>
				<div class="to">
					<div class="discount">{{Lang.dollarSign}}<span>{{exchangeActive.discount}}</span></div>
					<div class="logo">
						<img class="img-100" :src="exchangeActive.logo" alt="">
					</div>
					<div class="title ellipsis">{{exchangeActive.title}}</div>
				</div>
			</div>
			<div class="btn-exchange" @click="onExchangeSubmit">确认升级</div>
			<div class="close" @click="onTogglePopup('exchange')">
				<i class="icon icon-close font-20"></i>
			</div>
		</van-popup>
		<agreement
			:show="popup.agreement"
			title="常见问题与说明"
			:content="vipRedpacket.rules"
			@agreementHide="onTogglePopup('agreement')"
		>
		</agreement>
		<iloading v-show="preLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import Agreement from '@/components/agreement'
	export default {
		data() {
			return {
				member: {},
				vipRedpacket: {
					params: {},
					style: {},
					redpacket: {},
					powers: {}
				},
				meals: [],
				exchanges: {
					page: 2,
					psize: 15,
					empty: false,
					finished: false,
					data: []
				},
				mealActive: {},
				exchangeActive: {},
				redpacketActive: {},
				stores: [],
				popup: {
					meal: false,
					exchange: false,
					agreement: false,
				},
				showFixedBtn: false,
				preLoading: true
			}
		},
		components: {
			PublicHeader,
			Agreement
		},
		methods: {
			onLoad() {
				var that = this;
				that.util.request({
					url: 'vipRedpacket/index',
				}).then((res) => {
					that.preLoading = false;
					let result = res.data.message;
					if(result.errno) {
						that.util.$toast(result.message);
						return;
					}
					result = result.message;
					that.member = that.util.extend(that.member, result.member);
					that.vipRedpacket = that.util.extend(that.vipRedpacket, result.vipRedpacket);
					that.meals = [...result.meals];
					that.stores = [...result.stores];
					that.exchanges.data = [...that.exchanges.data, ...result.exchanges];
					if(!that.exchanges.data.length) {
						that.exchanges.data.empty = true;
					}
					if(result.exchanges && result.exchanges.length < that.exchanges.psize) {
						that.exchanges.finished = true;
					}
					that.util.setWXTitle(that.vipRedpacket.params.title);
				})
			},
			onBuyMeal(id) {
				var that = this;
				that.util.request({
					url: 'vipRedpacket/index/meal',
					data: {
						id: id
					},
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						that.util.$toast(result.message);
						return;
					}
					result = result.message;
					that.mealActive = that.util.extend(that.mealActive, result.meal);
					that.onTogglePopup('meal');
				})
			},
			onSubmit() {
				var that = this;
				that.util.request({
					url: 'vipRedpacket/index/meal',
					data: {
						id: that.mealActive.id
					},
					method: 'POST',
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						that.util.$toast(result.message);
						return;
					}
					result = result.message;
					that.$router.push(that.util.getUrl({path: 'pages/public/pay', query: {order_id: result.id, order_type: 'vipRedpacket'}}));
				})
			},
			onExchange(id) {
				var that = this;
				if(that.vipRedpacket.params.exchangeStatus != 1) {
					return false;
				}
				that.util.request({
					url: 'vipRedpacket/index/exchange',
					data: {
						exchange_id: id
					},
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						that.util.$toast(result.message);
						return;
					}
					result = result.message;
					that.exchangeActive = that.util.extend(that.exchangeActive, result.exchange);
					that.redpacketActive = that.util.extend(that.redpacketActive, result.redpacket);
					that.onTogglePopup('exchange');
				})
			},
			onExchangeSubmit() {
				var that = this;
				if(that.vipRedpacket.params.exchangeStatus != 1) {
					return false;
				}
				that.util.request({
					url: 'vipRedpacket/index/exchange',
					data: {
						exchange_id: that.exchangeActive.id,
						redpacket_id: that.redpacketActive.id
					},
					method: 'POST',
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						that.util.$toast(result.message);
						return;
					}
					that.util.$toast(result.message);
					that.onTogglePopup('exchange');
				})
			},

			onLoadMore() {
				var that = this;
				that.util.request({
					url: 'vipRedpacket/index/more',
					data: {
						page: that.exchanges.page,
						psize: that.exchanges.psize,
					}
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						that.util.$toast(result.message);
						return;
					}
					result = result.message;
					that.exchanges.data = [...that.exchanges.data, ...result.exchanges];
					if(result.exchanges && result.exchanges.length < that.exchanges.psize ) {
						that.exchanges.finished = true;
					}
					that.exchanges.page++;
				})
			},
			onTogglePopup(key) {
				var that = this;
				that.popup[key] = !that.popup[key];
			}
		},
		mounted() {
			var that = this;
			that.onLoad();
			window.addEventListener('scroll', function() {
				var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
				if(scrollTop > 120 && !that.showFixedBtn) {
					that.showFixedBtn = true;
