		border-radius: 50px;
		background-color: #3B322C;
		color: #FBE8D8;
	}
	#vip-redpacket-index .popup-exchange .close{
		display: flex;
		align-items: center;
		justify-content: center;
		position: absolute;
		bottom: -60px;
		left: 50%;
		margin-left: -20px;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		border: 1px solid #A7A7A7;
		color: #fff;
	}
</style>


